body {
  background: rgb(59, 190, 99);
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Nunito';
  font-weight: bold;
}

.title {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 700;
  background: rgb(222, 248, 200);
  color: #357949;
}

.app {
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color:#357949;
}

th, td{
  border: 1px solid #357949;
}

.pricecol{
  text-align: right;
}

h2 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  padding: 10px;
  background-color: rgb(222, 248, 200);
  font-size: 30px;
  color: #357935;
  text-shadow: 3px 3px 3px #ababab;
}

label {
  font-size: 18px;
  margin-right: 5px;
  color:aliceblue;
}

.containerInput {
  align-items: center;
  text-align: center;
  margin-top: 20px;
}

form {
  margin-bottom: 10px;
}

.row-list {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

thead{
  background-color: aliceblue;
  text-align: center;
}

tr {
  background-color: rgb(222, 248, 200);
}

td {
  padding: 2px 5px 2px 5px;
}

input {
  padding: 3px;
  color: darkcyan;
  border: 4px solid;
  border-radius: 10px;
  background-color:aliceblue;
  font-size: 16px;
  margin-right: 5px;
}

.buttonwrap {
  text-align: center;
}

button {
  padding: 0 8px;
  margin: 0 4px;
  border-radius: 8px;
  color: rgb(127, 0, 139);
  background-color: rgb(96, 244, 96);
  cursor: pointer;
  font-size: 15px;
}

button:hover {
  color: #ffffff;
}

button:active {
  background-color: antiquewhite;
  transform: translateY(2px);
}

.btns {
  float: right;
  margin-right: 10px;
  display: flex;
  justify-content: center;
}

p {
  color: antiquewhite;
  margin-top: 30px;
  bottom: 20px;
  width: 80%;
  position: relative;
  text-align: center;
}

.mt{
  background-color:#357949;
  border-style: none;
}

@media screen and (min-width: 1500px) {
  
}

@media screen and (max-width: 540px) {
  
  

  .btns {
    margin-right: 0;
    float: right;
    display: grid;
    justify-content: left;
  }

  

  button {
    width: 70px;
    margin: 3px 0;
    font-size: 12px;
  }

  form button {
    margin-left: 5px;
    height: 30px;
    width: 80px;
    font-size: 14px;
  }

  tr, thead {
    font-size: 14px;
  }
  .buttonwrap{
    float: none;
    align-self: center;
    width: 70px;
  }
  .buttonwrap button{
    padding: 0px 5px;
  }

  .containerInput{
    display: flex;
    flex-direction: column;
  }

  .row-list {
    align-items: center;
    width: auto;
  }
  input{
    margin-top: 3px;
    margin-bottom: 3px;
  }
}